/* stylelint-disable */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.header {
  padding-top: 36px;
}
.header__logo {
  display: flex;
}
.header__logo img {
  margin-top: 10px;
  width: 56px;
  height: 34px;
  margin-right: 12px;
}
.header__logo span {
  font-family: "Cedarville Cursive", cursive;
  font-weight: 400;
  font-size: 32px;
  line-height: 60px;
  color: #000;
}
.header__menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
}
.header__item {
  margin-right: 90px;
}
.header__item:last-child {
  margin-right: 0px;
}
.header__link {
  color: #444A4A;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
}
.header__link_activ {
  color: #fff;
  padding: 13px 32px;
  background-color: #2D635E;
  border-radius: 6px;
}

.header-wrp {
  display: flex;
  align-items: center;
  justify-content: space-between;
}